	import back from '@/components/back'
	import noData from '@/components/noData'

	export default {
		components: {
			back,
			noData
		},
		data() {
			return {
				search: '',
				show3: false,
				title: '找医生',
				sortList: [
					'全部医院',
					'科室',
					'筛选'
				],
// 				sortList2: [{
// 						SortField: "",
// 						name: "综合排序"
// 					},
// 					{
// 						SortField: "ConsultCount",
// 						name: "护理咨询量"
// 					},
// 					{
// 						SortField: "ConsultVolume",
// 						name: "图文问诊量"
// 					},
// 					{
// 						SortField: "VolumeVideo",
// 						name: "视频问诊量"
// 					},
// 					{
// 						SortField: "PraiseRate",
// 						name: "好评率"
// 					},
// 					{
// 						SortField: "ResponseRate",
// 						name: "24小时回复率"
// 					},
// 					{
// 						SortField: "PatientAttentDrNum",
// 						name: "关注量"
// 					},
// 					{
// 						SortField: "MedicalRecord",
// 						name: "第二诊疗"
// 					},
// 					{
// 						SortField: "MedicalPrice",
// 						name: "第二诊疗价格"
// 					},
// 					{
// 						SortField: "InterVolume",
// 						name: "问诊量"
// 					}
// 				],
				sortList2: [{
						code: "1101",
						name: "图文问诊"
					},
					{
						code: "1102",
						name: "视频问诊"
					},
					{
						code: "1103",
						name: "名医续方"
					},
					{
						code: "1201",
						name: "远程咨询"
					},
					{
						code: "1202",
						name: "远程护理"
					},
					{
						code: "1110",
						name: "第二诊疗意见"
					},
				],
				sortList3: [{
						code: "1",
						name: "有服务包"
					},
					{
						code: "0",
						name: "无服务包"
					},
				],
				sortIdx: null,
				page: 1,
				page2: 1,
				hospital: [],
				Department: [],
				secDepartment: [],
				HospitalLevels: [],
				Positions: [],
				doctors: [],
				haveMore: true,
				haveMore2: true,
				hos: {
					HospitalId: -1
				},
				dep: null,
				secDep: {
					DepartmentId: -1
				},
				sort: null,
				IsHasPack: -1,
				SericeItemCode: null,
				HospitalLevel: null,
				PositionPick: null,
				area:this.$store.state.area,
				pageShow: false,
				scrollTopDistance: 0,
				loading: false,
				inputFlag: false,
				showConfirm: false, //是否显示弹窗
				selectDrMsg: null,
				loginDrMsg:this.$store.state.userInfo.Data.User,
				ListDetails:this.$store.state.preList,
				allSpa:this.$store.state.allSpa,
				user: this.$store.state.userInfo.Data,
				defaultImg: 'this.src="' + require('../../assets/prefRecommend/1.jpg') + '"',
				act3:null,
			}
		},
		watch: {
			sortIdx(n) {
				if(n === null) {
					this.$refs.el3.style = 'overflow-y: auto'
				} else {
					this.$refs.el3.style = 'overflow-y: hidden;'
				}
			}
		},
		created() {
			//this.getArea()
			//console.log(this.allSpa)
		},
		mounted() {
			this.getMore()
			this.reload()
		},
		beforeRouteLeave(to,from,next){
			window.onscroll = null
			if(from.meta.keepAlive) {
		　       from.meta.scollTopPosition = this.scrollTopDistance; 
			}
			next()
		},
		beforeRouteEnter(to, from, next) {  //进入前    	
			next(vm => {
				vm.getMore()
				vm.ListDetails = vm.$store.state.preList
				if(!vm.$store.state.area){
					vm.area = {
						AreaName: '全部地区',
						AreaId: -1
					}
					vm.search = ''
					vm.hosPick({HospitalId: -1,HospitalName:'全部医院'})
					vm.depPick({DepartmentId: -1})
					vm.PositionPick = null
					vm.HospitalLevel = null
					vm.IsHasPack = -1
					vm.allSpa = vm.$store.state.allSpa
					vm.getArea()
				}else if(!vm.area || (vm.$store.state.area && vm.area.AreaId != vm.$store.state.area.AreaId) || (from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || vm.$store.state.isFresh){
					vm.area = vm.$store.state.area
					vm.sortIdx = null
					vm.page2 = 1
					vm.page = 1
					vm.haveMore2 = true
					vm.haveMore = true
					to.meta.scollTopPosition = 0
					vm.doctors = []
					vm.pageShow = false
					vm.getArea()
				}else{
					setTimeout(()=>{
						vm.$refs.el3.scrollTop = to.meta.scollTopPosition;   
					},1)
				}
			});
		},
		
		beforeDestroy() {
			window.onscroll = null
		},
		methods: {
			sortDoc(idx) {
				if(this.sortIdx === idx) {
					this.sortIdx = null
				} else {
					this.sortIdx = idx
				}
			},
			searchFor() {
				if(this.inputFlag) {
					return
				}
				this.page2 = 1
				this.pull(false)
			},
			chooseDr(i){
				this.allSpa.GoodsNum = 1
				this.allSpa.DrId = i.DrId
				this.allSpa.DrName = i.DrName
				this.allSpa.Price = i.Price
				this.allSpa.PositionName = i.PositionName
				this.allSpa.HospitalName = i.HospitalName
				this.allSpa.RecommondAmount = i.RecommondAmount
				if(this.ListDetails && this.ListDetails.length){
					let t = 0
					this.ListDetails.forEach((v,k) => {
					    if(v.GoodsSource == this.allSpa.GoodsSource && v.GoodsId == this.allSpa.GoodsId  && v.SpecialistHosGroupId == this.allSpa.SpecialistHosGroupId){  
							t = 1
							this.ListDetails.splice(k, 1, this.allSpa)
						}
					})
					if(t == 0){
						this.ListDetails.push(this.allSpa)
					}
				}else{
					this.ListDetails.push(this.allSpa)
				}
				this.$store.dispatch('pushpreList',this.ListDetails)
				this.$router.back()
			},
			getArea() {
				if(!this.area){
					this.area = {
						AreaName: '全部地区',
						AreaId: -1
					}
					this.search = ''
				}
				this.pull(false)
				this.pullHospital(false)
				this.pullDepartment(0)
				this.pullElse()
			},
			pullHospital(h) {
				this.$post('PlatFormAPI/Doctor/QueryDrHospitalPage', {
					HospitalLevel: -1,
					AreaId: this.area ? this.area.AreaId : 0,
					PageIndex: this.page,
					PageSize: 15
				}).then(o => {
					if(!o.ReturnData || o.ReturnData.length < 15) {
						this.haveMore = false
					} else {
						this.haveMore = true
					}

					if(h) {
						o.ReturnData.forEach(v => {
							this.hospital.push(v)
						})
					} else {
						this.hospital = o.ReturnData
					}
				})
			},
			pullDepartment(id) {
				this.$post('PlatFormAPI/Doctor/QueryDepartmentPage', {
					ParentId: id
				}).then(o => {
					if(id === 0) {
						this.Department = o.ReturnData
					} else {
						this.secDepartment = o.ReturnData
					}
				})
			},
			pullElse() {
				this.$post('PlatFormAPI/Category/QueryCategoryItem', {
					'CategoryIds': [
						9,
						10
					]
				}).then(o => {
					this.Positions = o[0].Items
					this.HospitalLevels = o[1].Items
				})
			},
			pull(h) {
				let AreaId = 0
				if(this.area){
				    AreaId = this.area.AreaId
				}
				this.$post('PlatFormAPI/SpecialistHosGroup/GetMallGoodsDoctorList', {
					DrId:this.loginDrMsg.DoctorId,
					KeyName: this.search,
					DrType:-1, //医生类别(0-无，1-医生，2-护士,3-药师),-1查询所有
					HospitalId: this.hos.HospitalId,
					DepartmentId: this.secDep.DepartmentId,
					Position: this.PositionPick ? this.PositionPick.CategoryItemValue : -1,
					OrgId:this.user.User.OrgId,
					HospitalLevel: this.HospitalLevel ? this.HospitalLevel.CategoryItemValue : -1,
					IsHasPack: this.IsHasPack,
					HosGroupId:-1,//医联体Id(医联体微信公众号进来有值),-1查询所有
					SericeItemCode: this.$store.state.allSpa.GoodsNo,//this.SericeItemCode,
					PageIndex: this.page2,
					PageSize: 15,
					AreaId
				}).then(o => {
					this.show3 = false
					this.loading = false
					if(!o.ReturnData || o.ReturnData.length < 15) {
						this.haveMore2 = false
					} else {
						this.haveMore2 = true
					}
					if(h) {
						o.ReturnData.forEach(v => {
							this.doctors.push(v)
						})
					} else {
						this.doctors = o.ReturnData
					}
					this.pageShow = true
				})
			},
			hosPick(i) {
				this.hos = i
				this.sortList[0] = i.HospitalName
				this.sortIdx = null
				this.page2 = 1
				this.pull(false)
			},
			depPick(i) {
				this.dep = i
				if(i.DepartmentId === -1) {
					this.secDepartment = []
					this.secDep = i
					this.sortList[1] = '全部科室'
					this.sortIdx = null
					this.page2 = 1
					this.pull(false)
				} else {
					this.pullDepartment(i.DepartmentId)
				}
			},
			secDepPick(i) {
				this.secDep = i
				this.sortList[1] = i.DepartmentName
				this.sortIdx = null
				this.page2 = 1
				this.pull(false)
			},
			sortPick(i) {
				this.sort = i
				this.sortList[2] = i.name
				this.sortIdx = null
				this.page2 = 1
				this.pull(false)
			},
			reset() {
				this.IsHasPack = -1,
					this.SericeItemCode = null,
					this.HospitalLevel = null,
					this.PositionPick = null
			},
			confirm() {
				this.sortIdx = null
				this.page2 = 1
				this.pull(false)
			},

			getMore() {
				//加载更多
				const el = this.$refs.el
				let timer = null
				el.onscroll = () => {
					clearTimeout(timer)
					timer = setTimeout(() => {
						const top = el.scrollTop ||
							el.pageYOffset ||
							el.scrollTop,
							h = el.scrollHeight,
							wh = el.offsetHeight
						if(h - top - wh < 100 && this.haveMore) {
							this.page += 1
							this.pullHospital(true);
						}
					}, 100)
				}
				const el2 = this.$refs.el3
				let timer2 = null
				el2.onscroll = () => {
					clearTimeout(timer2)
					timer2 = setTimeout(() => {
						const top = el2.scrollTop ||
							el2.pageYOffset ||
							el2.scrollTop,
							h = el2.scrollHeight,
							wh = window.innerHeight
						this.scrollTopDistance = top;
						if(h - top - wh < -90 && this.haveMore2) {
							this.page2 += 1
							this.loading = true
							this.pull(true);
						}
					}, 100)
				}
			},
			reload() {
				const el2 = this.$refs.el2
				const el3 = this.$refs.el3
				let startX = null
				let startY = null
				let endX = null
				let endY = null
				el2.addEventListener('touchstart', e => {
					this.show3 = false
					startX = e.changedTouches[0].pageX
					startY = e.changedTouches[0].pageY
				})
				el2.addEventListener('touchmove', e => {
					endX = e.changedTouches[0].pageX
					endY = e.changedTouches[0].pageY
					if(this.sortIdx !== null) {
						return
					}
					if(Math.abs(startX - endX) < Math.abs(startY - endY) && (startY - endY) < 0) {
						const top = el3.scrollTop ||
							el3.pageYOffset
						if(top < 10 || !top) {
							this.show3 = true
						}
					}
				})
				el2.addEventListener('touchend', () => {
					if(this.show3) {
						this.page2 = 1
						this.haveMore2 = true
						this.pull(false)
					}
				})
			}
		}
	}



